@import "common.scss";

.popup__page {
   position: absolute;
   width: 30%;
   height: 20%;
   border-radius: 5px;
   background: #F8BE4D;
   display: flex;
   justify-content: center;
   align-items: center;
   top: 25%;
   left: 50%;
   transform: translateX(-50%);
   z-index: 10;
}
.none-popup {
   display: none;
}
.popup__body {
   display: flex;
   flex-direction: column;
   align-items: center;
   gap: 25px;
   padding: 20px 25px;
}
.popup__tile {
   color: #121920;
   font-family: 'Lato', sans-serif;
   @include adaptiveFont(20, 14);
   font-style: normal;
   font-weight: 700;
   line-height: 24px; 
   text-align: center;
}
.close-button {
   padding: 10px 15px;
   border-radius: 5px;
   border: 2px solid #1B242D;;
   background: inherit;
   cursor: pointer;
   &:hover {
      color: #fff;
      border-color: #fff;
   }
 }
